.main {
  margin-top: 2rem;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card:link,
.card:visited {
  color: $white;
  font-size: 2rem;

  margin: 2rem;
  
  width: 300px;
  height: 180px;

  background-color: $background;
  border: 2px solid transparent;
  border-radius: 10px;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: .2s;

  &:hover,
  &:focus {
    color: $primary;
    border-color: currentColor;
  }

}